body {
  font-family: Arial, sans-serif;
  background: linear-gradient(to right, #74ebd5, #acb6e5);
  color: #333;
  margin: 0;
  padding: 0;
}

nav ul {
  background: #001f3f;
  padding: 10px;
}

nav ul li {
  display: inline;
  margin-right: 15px;
}

form input,
form select,
form button {
  margin: 10px;
  padding: 8px;
  width: 80%;
}
