/* General Container Styling */
.container {
    max-width: 400px; /* Keep form compact */
    margin: 50px auto;
    padding: 15px; /* Add padding around the form */
  }
  
  /* Card Styling */
  .card {
    border-radius: 10px;
    overflow: hidden;
  }
  
  /* Card Header */
  .card-header {
    text-align: center;
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  /* Card Body Form */
  .card-body {
    background-color: rgb(213, 235, 251);
    padding: 20px; /* Spacing inside the card body */
  }
  
  /* Form Input Styling */
  .form-group {
    margin-bottom: 15px; /* Add space between inputs */
  }
  
  .form-control {
    width: 100%;
    padding: 8px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
  }
  
  /* Add Right Padding */
  .card-body {
    padding-right: 25px; /* Additional padding on the right */
  }
  
  /* Button Styling */
  button {
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 15px;
    cursor: pointer;
    width: 100%;
    font-size: 16px;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  /* Dropdown Styling */
  select.form-control {
    font-size: 14px;
    padding: 8px;
    color: #333;
  }
  