.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #000080; /* Navy background */
    padding: 1rem 2rem; /* Add spacing around elements */
    color: white;
  }
  
  .logo {
    font-size: 1.8rem;
    font-weight: bold;
    color: #87CEEB; /* Light blue color for the heading */
    margin: 0;
  }
  
  .nav-links {
    display: flex;
    gap: 1.5rem; /* Space between links */
  }
  
  .nav-links a {
    text-decoration: none;
    color: white;
    font-size: 1.2rem;
    font-family: "Arial", sans-serif;
  }
  
  .nav-links a:hover {
    color: #FFD700; /* Gold color for hover effect */
  }
  